import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormArray, FormControl } from '@angular/forms';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-host-form',
  templateUrl: './host-form.component.html',
  styleUrls: ['./host-form.component.css']
})
export class HostFormComponent implements OnInit {

  pTitle: string = `Personal Info`;
  locTitle: string = `Your Belongings`;
  prefTitle: string = `Preferences`;
  addleadform: any = FormGroup;
  submitted = false;
  sessions = [
    { title: "Sunday", value: "Sun" },
    { title: "Monday", value: "Mon" },
    { title: "Tuesday", value: "Tue" },
    { title: "Wednesday", value: "Wed" },
    { title: "Thrusday", value: "Thr" },
    { title: "Friday", value: "Fri" },
    { title: "Saturday", value: "Sat" }
  ];

  websiteList: any = [
    { id: 1, name: 'Linoleum / Dance floor'},
    { id: 2, name: 'Padded / Gym floor'},
    { id: 3, name: 'Trampoline'},
    { id: 4, name: 'Grass'}
  ];
  isSubmitted: boolean = false;
  thanks : boolean = false;
  feedback : string = `Thank for your Feedback.`;
  dashboard : string = `Back to Home`;
  routeToHome : string = `/dashboard`;

  constructor(private _fb: FormBuilder, private _ds : DataService) {}

  ngOnInit(): void {
    this.addLead();
  }

  addLead() {
    this.addleadform = this._fb.group({
      name: [
        "",
        [
          Validators.required,
          Validators.maxLength(50),
          Validators.pattern("^[a-zA-Z ]+$"),
        ],
      ],
      phoneNo: ["",[Validators.minLength(10),Validators.maxLength(10),Validators.pattern("^[0-9]*$")]],
      emailAddress: ["", [Validators.required, Validators.email]],
      cityAndstreetInfo: ["null"],
      spotNeeded: ["null"],
      spotSize: ["", [Validators.required]],
      preticerInfo: ["null"],
      firstPreticeSession: ["null"],
      timeFrom: ["", [Validators.required]],
      timeUntil: ["", [Validators.required]],
      floorPreference: this._fb.array([], [Validators.required]),
      needHostSession: ["null"],
      notes: [""],
      unitLocation: ["", [Validators.required]],
      userType: ["Host"],
      costperDay: ["", [Validators.required]],
      costperMonth: ["", [Validators.required]],
      spotDesc: ["", [Validators.required]],
      renterTour: ["",[Validators.required]],
      renterPractice: ["", [Validators.required]],
      musicPrefernce :  ["", [Validators.required]],
    });
  }

  get g() { return this.addleadform.controls; };

  addlead() {
    if(this.addleadform.invalid){
      this.isSubmitted = true;
      return;
    }
    if(this.addleadform.value){
      if(this.addleadform.value.floorPreference.length){
        this.addleadform.value.floorPreference = this.addleadform.value.floorPreference.toString();
        this._ds.addLead('http://52.22.88.240:8000/publish-lead/', this.addleadform.value).subscribe(res =>{
          if(!!res && !!res.status){
            console.log(res);
            this.addleadform.reset();
            this.thanks = true;
          }
        },error =>{
          console.log(error);
        });
      }
    }
  }

  onChange(ev: any) {
    console.log(ev);
  }

 
  onCheckboxChange(e) {
    const website: FormArray = this.addleadform.get('floorPreference') as FormArray;
    if (e.target.checked) {
      website.push(new FormControl(e.target.value));
    } else {
       const index = website.controls.findIndex(x => x.value === e.target.value);
       website.removeAt(index);

    }

  }

}




